import axios from 'axios';
import { Post, Comment } from '../types';

const BASE_URL = 'http://ec2-13-114-45-36.ap-northeast-1.compute.amazonaws.com/api'; // Assuming that the API is served from the same domain as the frontend

const api = axios.create({
  baseURL: BASE_URL,
  withCredentials: true, // Send cookies along with requests for authentication
});

// Define functions to interact with the API endpoints

// Get all posts with comments and pagination ability
export const getAllPosts = async (page?: number) => {
  try {
    const response = await api.get(`/posts${page ? `?page=${page}` : ''}`);
    return response.data;
  } catch (error) {
    throw new Error('Failed to fetch posts.');
  }
};

// Add a like to a post by post ID
export const likePost = async (postId: string) => {
  try {
    const response = await api.post(`/posts/${postId}/like`);
    return response.data;
  } catch (error) {
    throw new Error('Failed to like the post.');
  }
};

// Get all comments related to a specific post by post ID
export const getCommentsByPostId = async (postId: string): Promise<Comment[]> => {
  try {
    const response = await api.get(`/posts/${postId}/comments`);
    const { comments } = response.data
    return comments;
  } catch (error) {
    throw new Error('Failed to fetch comments for the post.');
  }
};

// Get all comments related to a specific post by post ID
export const getPostById = async (postId: string): Promise<Post> => {
  try {
    const response = await api.get(`/posts/${postId}`);
    return response.data.post;
  } catch (error) {
    throw new Error('Failed to fetch comments for the post.');
  }
};

// Add a like to a comment by comment ID
export const likeComment = async (commentId: string) => {
  try {
    const response = await api.post(`/comments/${commentId}/like`);
    return response.data;
  } catch (error) {
    throw new Error('Failed to like the comment.');
  }
};

// Sign up a new user
export const signUpUser = async (userData: any) => {
  try {
    const response = await api.post('/signup', userData);
    return response.data;
  } catch (error) {
    throw new Error('Failed to sign up the user.');
  }
};

// Verify user email after signing up
export const verifyUserEmail = async (uniqueIdentifier: string) => {
  try {
    const response = await api.get(`/verify/${uniqueIdentifier}`);
    return response.data;
  } catch (error) {
    throw new Error('Failed to verify user email.');
  }
};

// Sign in and receive a JWT token in cookies
export const signInUser = async (credentials: { username: string; password: string }) => {
  try {
    const response = await api.post('/auth/signin', credentials);
    return response.data;
  } catch (error) {
    throw new Error('Failed to sign in.');
  }
};

// Sign in and receive a JWT token in cookies
export const verifiyUserLogedin = async () => {
  const token = localStorage.getItem('token');
  if (token) {
    axios.defaults.headers.common['Cookie'] = token;

    try {
      const response = await axios.get('/api/auth/verify');
      return response.data.user;
    } catch (error) {
      return null;
    }
  }
  return null
};

// Sign out and clear the JWT token from cookies
export const signOutUser = async () => {
  try {
    const response = await api.get('/signout');
    return response.data;
  } catch (error) {
    throw new Error('Failed to sign out.');
  }
};

// Save a user's profile photo
export const saveUserProfilePhoto = async (userId: string, photoData: any) => {
  try {
    const response = await api.post(`/users/${userId}/profile-photo`, photoData);
    return response.data;
  } catch (error) {
    throw new Error('Failed to save the user profile photo.');
  }
};

// Update a post with a new comment text, given post ID and user credentials
export const updatePostWithComment = async (postId: string, commentData: any) => {
  try {
    const response = await api.post(`/posts/${postId}/comments`, commentData);
    return response.data;
  } catch (error) {
    throw new Error('Failed to update the post with a new comment.');
  }
};

// Save a new post with text and an optional photo using user credentials
export const saveNewPost = async (postData: any) => {
  try {
    const response = await api.post('/posts', postData);
    return response.data;
  } catch (error) {
    throw new Error('Failed to save the new post.');
  }
};

// Reset user password (for forgot password feature)
export const resetUserPassword = async (email: string) => {
  try {
    const response = await api.post('/reset-password', { email });
    return response.data;
  } catch (error) {
    throw new Error('Failed to reset the user password.');
  }
};

export default api;
