import React from 'react'
import { PostProps } from '../types'
import { Link } from 'react-router-dom'


const Post = (props: PostProps) => {
  return (
    <Link to={`post/${props._id}`}>
      <p>{props.text}</p>
      <div>{props.likes}</div>
      <p>{props.photo}</p>
      <p>{props.userId}</p>
    </Link>
  )
}

export default Post