import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Post, Comment } from '../types';
import { getCommentsByPostId, getPostById } from '../services/api'; // Implement the API service function to fetch comments for a post.

const PostDetailsPage: React.FC = () => {
  const { postId } = useParams<{ postId: string }>();
  const [post, setPost] = useState<Post | null>(null);
  const [comments, setComments] = useState<Comment[]>([]);

  useEffect(() => {
    // Fetch post details and comments and set them to the state.
    
    const fetchPostDetails = async () => {
      try {
        // Fetch post details using the postId
        // set the fetched post to the state
        // Fetch comments for the post and set them to the state
        const post = await getPostById(postId!) as Post
        const comments = await getCommentsByPostId(postId!)
        setComments(comments)
        setPost(post)
      } catch (error) {
        console.error('Error fetching post details:', error);
        setComments([])
      }
    };
    fetchPostDetails();
  }, [postId]);

  if (!post) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <h2>{post.text}</h2>
      <p>{post.likes}</p>
      <h3>Comments</h3>
      <ul>
        {comments.map((comment) => (
          <li key={comment._id}>
            {/* Display comment information */}
          </li>
        ))}
      </ul>
      {/* Add new comment form */}
    </div>
  );
};

export default PostDetailsPage;
